@import 'master';

html,
body {
  font-size: 12px;
}

@media (max-width: 1200px) {
}
@media (max-width: 992px) {
  .header {
    .header-top-content {
      display: none;
    }
    .header-bottom-content {
      display: none;
    }
    .header-fixed {
      display: block;
      .container {
        max-width: none;
      }
      .header-fixed-inner {
        min-height: 0;
        .hamburger {
          display: inline-block;
        }
        .social {
          display: none;
        }
        .branding {
          margin-bottom: 15px;
        }
        .contact-phone {
          display: none;
        }
        .nav-bar {
          .nav-menu {
            display: none;
            &.active {
              display: flex;
            }
            background: $secondary;
            position: absolute;
            right: 0;
            top: 105px;
            flex-direction: column;
            align-items: flex-end;
            .nav-menu-item {
              margin-right: 15px !important;
            }
          }
        }
      }
    }
  }
  .content {
    .projects-carousel-section {
      .project-item {
        .project-item-overlay {
          &::before,
          &::after {
            display: none;
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
}
@media (max-width: 576px) {
  .content {
    .landing-section {
      .landing-content {
        padding: 50px 20px;
      }
    }
    .features-line-section {
      .features-top {
        .left {
          .img-wrapper {
            .label {
              right: 0;
              left: 50%;
              transform: translate(-50%, -50%);
            }
          }
        }
        .right {
          display: none;
        }
      }
      .features-bottom {
        margin-top: 130px;
      }
    }
  }
  .footer {
    .branding {
      display: none;
    }
  }
}
